/* === Base Styles === */
.app-root {
  min-height: 100vh;
  color: #fff;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  position: relative;
}

/* === Search Bar === */
.search-form {
  position: absolute;
  top: 10rem;
  left: 54%;
  transform: translateX(-50%);
  width: 80%;
  max-width: 800px;
  padding: 0;
  box-sizing: border-box;
}

.search-input {
  width: 100%;
  max-width: 600px;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 1rem 2rem;
  color: #9a9a9a;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  -webkit-backdrop-filter: blur(10px);
  border: none;
  border-radius: 1rem;
}

.search-input:focus {
  opacity: 0.8;
  outline: none;
}

.search-widget {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

/* === Widget Grid === */
.widgets {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 600px));
  justify-content: center;
  gap: 10rem; /* slightly more spacing */
  width: 100%;
  padding: 2rem;
  box-sizing: border-box;
  align-content: center;
}

.widget {
  width: 100%;
  min-height: 620px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* === Now Playing === */
.now-playing-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  min-height: 620px;
  transition: all 0.3s ease-in-out;
}

.album-art-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  padding: 10px;
}

.album-art-bg {
  width: 600px;
  height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  will-change: transform;
}

.album-art {
  width: 100%;
  height: 100%;
  min-height: 300px;
  object-fit: cover;
  border-radius: 20px;
}

.track-info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
  width: 600px;
  margin: 10px 0 0 0;
}

.track-name {
  font-size: 2.5rem;
  font-weight: bold;
  color: rgba(188, 188, 188, 0.8);
  margin: 10px 0;
}

.track-artists {
  font-size: 1.7rem;
  font-weight: normal;
  color: rgba(188, 188, 188, 0.8);
  margin: 0;
}

.nothing-playing {
  display: inline-block;
  font-size: 1.2rem;
  font-weight: 600;
  color: #9a9a9a;
  text-align: center;
  padding: 0.75rem 1.5rem;
  border-radius: 1rem;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  border: 0px solid rgba(255, 255, 255, 0);
  margin: 0 auto;
}

/* === Calendar Widget Container === */
.calendar-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

/* === Utility === */
.hidden {
  display: none !important;
}

.login-button {
  display: block;
  font-size: 1rem;
  font-weight: 600;
  color: #9a9a9a;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: none;
  padding: 1rem 2rem;
  border-radius: 1rem;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  max-width: 30%;
  margin: 15rem auto;
  text-align: center;
}
